<template>
    <v-container id="template">
        <v-btn  id="print" class="blue white--text mr-4 mb-5" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="red white--text mb-5" to="/entrycpkb" link>Back</v-btn>
        <v-row id="print" no-gutters>
            <v-row no-gutters>
                <v-col cols="2">
                    <h3 class="mt-2">No LP</h3>
                </v-col>
                <v-col cols="2">
                    <v-autocomplete single-line outlined dense="" :items="dbelicpkbs" class="title" item-text="No LP"
                    item-value="No LP" label="Nomor LP" v-model="noLP" @change="getDataCPKB()">
                    <template slot="item" slot-scope="data">
                        {{ data.item["No LP"] }} - {{ data.item["Kode Barang CPKB"] }}
                    </template>
                </v-autocomplete>
                </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-right mr-4">Nama Barang</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title"  dense single-line outlined  label="Nama Barang" v-model="dbelicpkbforms['Nama Barang']"></v-text-field>
                </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-right mr-4">Kode Barang</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title"   dense single-line outlined label="Kode Barang" v-model="dbelicpkbforms['Kode Barang']"></v-text-field>
                </v-col>
            </v-row>
            <v-row no-gutters>
                <v-col cols="2">
                    <h3 class="mt-2">No Bets</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title"  dense single-line outlined label="No Bets" v-model="dbelicpkbforms['No Bet']"></v-text-field>
                </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-right mr-4">Pemasok</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title"  dense single-line outlined label="Pemasok" v-model="dbelicpkbforms['Nama Suplier']"></v-text-field>
                </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-right mr-4">Jumlah</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title" type="number" dense single-line outlined label="Jumlah" v-model="jumlah"></v-text-field>
                </v-col>
            </v-row>
            <v-row no-gutters>
                <v-col cols="2">
                    <h3 class="mt-2">No COA</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title"  dense single-line outlined label="No COA" v-model="noCOA"></v-text-field>
                </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-right mr-4">Jumlah Wadah</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title"  dense single-line outlined label="Jumlah wadah" v-model="noWadah"></v-text-field>
                </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-right mr-4">Tgl Terima</h3>
                </v-col>
                <v-col cols="2" style="display: flex">
                    <v-menu ref="menu" v-model="menu" :close-on-content-click="false" :return-value.sync="date"
                        transition="scale-transition">
                        <template v-slot:activator="{ on }">
                            <v-text-field dense single-line outlined v-model="dbelicpkbforms['Tanggal']" label="Tgl Terima"
                                class="title" readonly v-on="on"></v-text-field>
                        </template>
                        <v-date-picker v-model="dbelicpkbforms['Tanggal']" no-title scrollable>
                            <v-spacer></v-spacer>
                            <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                            <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
                        </v-date-picker>
                    </v-menu>
                </v-col>
            </v-row>
            <v-row no-gutters>
                <v-col cols="2">
                    <h3 class="mt-2">Kadaluwarsa</h3>
                </v-col>
                <v-col cols="2" style="display: flex">
                    <v-menu ref="menu1" v-model="menu1" :close-on-content-click="false" :return-value.sync="date"
                        transition="scale-transition">
                        <template v-slot:activator="{ on }">
                            <v-text-field dense single-line outlined v-model="kadaluwarsa" label="Kadaluwarsa"
                                class="title" readonly v-on="on"></v-text-field>
                        </template>
                        <v-date-picker v-model="kadaluwarsa" no-title scrollable>
                            <v-spacer></v-spacer>
                            <v-btn text color="primary" @click="menu1 = false">Cancel</v-btn>
                            <v-btn text color="primary" @click="$refs.menu1.save(date)">OK</v-btn>
                        </v-date-picker>
                    </v-menu>
                </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-right mr-4">Tgl Ambil Contoh</h3>
                </v-col>
                <v-col cols="2" style="display: flex">
                    <v-menu ref="menu2" v-model="menu2" :close-on-content-click="false" :return-value.sync="date"
                        transition="scale-transition">
                        <template v-slot:activator="{ on }">
                            <v-text-field dense single-line outlined v-model="tanggalAmbilContoh" label="Tgl Ambil Contoh"
                                class="title" readonly v-on="on"></v-text-field>
                        </template>
                        <v-date-picker v-model="tanggalAmbilContoh" no-title scrollable>
                            <v-spacer></v-spacer>
                            <v-btn text color="primary" @click="menu2 = false">Cancel</v-btn>
                            <v-btn text color="primary" @click="$refs.menu2.save(date)">OK</v-btn>
                        </v-date-picker>
                    </v-menu>
                </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-right mr-4">Diambil Oleh</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title" dense single-line outlined label="Oleh" v-model="oleh"></v-text-field>
                </v-col>
            </v-row>
        </v-row>
        <template id="template">
            <table>
                <tr v-for="i in parseInt(jumlah)" :key='i.id'>
                    <td style="padding-bottom: 40px;">
                    <table id="tabel" height="264px">
                    <tr>
                        <th id="td" width="128px"><img style="vertical-align: middle;" src="../../assets/logoNiagara.png" width="75px" height="20"></th>
                        <th id="td" width="220px" colspan="2" style="text-align:center;">KARANTINA <img style="vertical-align: middle;" src="../../assets/yellow.png" width="20px" height="20"></th>
                    </tr>
                    <tr>
                        <td id="td"><b> Nama Barang</b></td>
                        <td id="td" colspan="2" style="text-align:center;"><b>{{dbelicpkbforms['Nama Barang']}}</b></td>
                    </tr>
                    <tr>
                        <td id="td"><b>Kode Barang</b></td>
                        <td id="td" colspan="2"  style="text-align:center;"><b>{{dbelicpkbforms['Kode Barang']}}</b></td>
                    </tr>
                    <tr>
                        <td id="td"><b> No Bets </b></td>
                        <td id="td" colspan="2" style="text-align:center;"><b>{{dbelicpkbforms['No Bet']}}</b></td>
                    </tr>
                    <tr>
                        <td id="td"><b> Pemasok </b></td>
                        <td id="td" colspan="2" style="text-align:center;"><b>{{dbelicpkbforms['Nama Suplier']}}</b></td>
                    </tr>
                    <tr>
                        <td id="td"><b> No LP </b></td>
                        <td id="td" colspan="2" style="text-align:center;"><b>{{noLP}}</b></td>
                    </tr>
                    <tr>
                        <td id="td"><b> Jumlah </b></td>
                        <td id="td" colspan="2" style="text-align:center;"><b>{{jumlah}}</b></td>
                    </tr>
                    <tr>
                        <td id="td"><b> No COA</b></td>
                        <td id="td" colspan="2" style="text-align:center;"><b>{{noCOA}}</b></td>
                    </tr>
                    <tr>
                        <td id="td"><b> No Wadah</b></td>
                        <td id="td" colspan="2" style="text-align:center;"><b>{{noWadah}}</b></td>
                    </tr>
                    <tr>
                        <td id="td"><b> Tgl Terima</b></td>
                        <td id="td" width="85px" style="text-align: center;"><b>{{dbelicpkbforms['Tanggal'] }}</b></td>
                        <td id="td" rowspan="3" style="vertical-align: text-top; text-align:center;"><b> Contoh Diambil Oleh :</b><br><br><b> {{oleh}}</b></td>
                    </tr>
                    <tr>
                        <td id="td"><b> Kadaluwarsa</b></td>
                        <td id="td" style="text-align: center;"><b>{{kadaluwarsa}}</b></td>

                    </tr>
                    <tr>
                        <td id="td"><b> Tgl Ambil Contoh</b></td>
                        <td id="td" style="text-align: center;"><b>{{tanggalAmbilContoh}}</b></td>

                    </tr>
                </table>
                </td>
                </tr>
            </table>
        </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                date: new Date().toISOString().substr(0, 10),
                menu: false,
                menu1: false,
                menu2: false,
                noLP:'',
                jumlah: 0,
                noCOA: '',
                noWadah: '',

                kadaluwarsa: '',
                tanggalAmbilContoh: '',
                oleh: '',
                dbelicpkbs: [],
                dbelicpkbforms: [],
                spesifikasiforms: [],
                NoRules: [
                    value => (value || '').length == 4 || 'INPUTAN HARUS 4 Karakter',
                ],
                nomor:'',
                nomorUji:'',
                keyword: '',
                jumlahData: 1,
                load: false,
                laporanujibs: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'dbelicpkbs/dataLP'
                this.$http.get(uri).then(response => {
                    this.dbelicpkbs = response.data.data
                })
            },

             getDataCPKB() {
                 if(this.noLP){
                    var uri = this.$apiUrl + 'dbelicpkbs/dataLP/' + this.noLP
                    this.$http.get(uri).then(response => {
                        this.dbelicpkbforms = response.data.data[0]
                        console.log(this.dbelicpkbforms)
                    })
                 }
             },

            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style scoped>
    @media print {
        #print {
            display: none;
        }

        #template {
            background-color: white
        }
      
    }
    #tabel{
        border-collapse: collapse;
    }
    #tabel #th, #td {
        border: 1px solid black;
        font-size: 9pt
    }
</style>